:root {
  --set-margin: 30px;
  --set-margin-ico: 10px;
  --set-item: 4;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 450px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 738px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

html {
  box-sizing: border-box;
}

body {
  color: #757575;
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.71;
}

.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  display: block;
}

.list {
  list-style-type: none;
}

.link {
  color: currentColor;
  text-decoration: none;
}

.section__title {
  color: #212121;
  text-align: center;
  letter-spacing: .03em;
  margin-top: 0;
