}

.card-set {
  margin-left: calc(-1 * var(--set-margin) );
  margin-top: calc(-1 * var(--set-margin) );
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  margin-left: var(--set-margin);
  margin-top: var(--set-margin);
}

@media screen and (min-width: 768px) {
  .porfolio-work__list {
    margin-left: calc(-1 * var(--set-margin) );
    margin-top: calc(-1 * var(--set-margin) );
    flex-wrap: wrap;
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .porfolio-work__item:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 768px) {
  .porfolio-work__item {
    margin-bottom: 0;
    margin-left: var(--set-margin);
    margin-top: var(--set-margin);
    flex-basis: calc(50% - var(--set-margin) );
  }
}

@media screen and (min-width: 1200px) {
  .porfolio-work__item {
    margin-left: var(--set-margin);
    margin-top: var(--set-margin);
    flex-basis: calc(33.3333% - var(--set-margin) );
  }
}

.porfolio-work__link {
  height: 100%;
  flex-direction: column;
  transition-property: box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.porfolio-work__link:hover, .porfolio-work__link:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.porfolio-work__thumb {
  position: relative;
  overflow: hidden;
}

.porfolio-work__overlay {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  color: #fff;
  opacity: 1;
  background: rgba(33, 150, 243, .9);
  justify-content: center;
  align-items: center;
  padding: 24px;
  font-size: 18px;
  line-height: 1.55556;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.porfolio-work__link:hover .porfolio-work__overlay, .porfolio-work__link:focus .porfolio-work__overlay {
  opacity: 1;
  transform: translateY(0%);
}

@media screen and (min-width: 480px) {
  .porfolio-work__overlay {
    padding-top: 65px;
    padding-bottom: 65px;
  }
}

@media screen and (min-width: 768px) {
  .porfolio-work__overlay {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media screen and (min-width: 1200px) {
  .porfolio-work__overlay {
    padding-top: 63px;
    padding-bottom: 63px;
  }
}

.porfolio-work__block {
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  flex-grow: 1;
  padding: 20px 24px;
}

.porfolio-work__title {
  color: #212121;
  letter-spacing: .06em;
  margin-bottom: 4px;
  font-size: 18px;
  font-weight: 700;
  line-height: 2;
}

.porfolio-work__text {
  font-size: 16px;
  line-height: 1.88;
}

.porfolio-work__p {
  height: 100%;
  overflow: auto;
}

.filter {
  gap: 8px;
  margin-bottom: 43px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .filter {
    flex-wrap: wrap;
    justify-content: start;
  }
}

@media screen and (min-width: 768px) {
  .filter {
    justify-content: center;
  }
}

.filter__item {
  margin-bottom: 7px;
}

.filter__button {
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 6px 22px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.63;
  transition-property: color, background-color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.filter__button:hover, .filter__button:focus, .filter__button--active {
  color: #fff;
  background-color: #2196f3;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

